.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%; }

.page body {
  margin: 0;
  min-height: 100%;
  color: #ffffff;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  -ms-flex-line-pack: start;
      align-content: start;
  background-color: #4D5374; }

.page main {
  overflow: hidden; }

.page a:hover {
  opacity: 0.5; }

.page a:focus {
